@font-face {
  font-family: "icons";
  font-display: swap;
  src: url("../fonts/icons.woff") format("woff"), url("../fonts/icons.woff#icons") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
[class^=_icon-]::before,
[class*=_icon-]::before {
  font-family: "icons";
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^=_icon-]::before,
[class*=_icon-]::before {
  display: block;
}

.preloader {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 999999;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  transition: all 0.5s;
  opacity: 1;
}

.preloader-remove {
  opacity: 0;
  z-index: -10;
}

.preloader-2 {
  width: auto;
  height: auto;
}

.preloader-2 ul {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  animation: preloader-2-rot 16s linear infinite;
}

@keyframes preloader-2-rot {
  100% {
    transform: rotate(360deg);
  }
}
.preloader-2 li {
  width: 40px;
  height: 40px;
  background: #337ab7;
  border-radius: 4px;
  animation: preloader-2-scale 0.8s linear alternate infinite;
}

@keyframes preloader-2-scale {
  100% {
    transform: scale(0.1);
    opacity: 0;
  }
}
